import React, {Fragment} from 'react';
import PersonRoundedIcon from '@material-ui/icons/PersonRounded';
import MicOffIcon from '@material-ui/icons/MicOff';
import VolumeOffIcon from '@material-ui/icons/VolumeOff';
import ExitToAppRoundedIcon from '@material-ui/icons/ExitToAppRounded';
import ExpandMoreRoundedIcon from '@material-ui/icons/ExpandMoreRounded';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import TreeItem from '@material-ui/lab/TreeItem';
import {Typography, Grid, IconButton} from '@material-ui/core';
import BlockRoundedIcon from '@material-ui/icons/BlockRounded';
import DesktopWindowsRoundedIcon from '@material-ui/icons/DesktopWindowsRounded';
import RemoveRoundedIcon from '@material-ui/icons/RemoveRounded';
import Tooltip from '@material-ui/core/Tooltip';
import DeleteRoundedIcon from '@material-ui/icons/DeleteRounded';
import styled from 'styled-components';


const ThemedTreeItem = styled(TreeItem)`
    background-color: ${({ theme }) => theme.contrast};
    color: ${({ theme }) => theme.text};
    user-select: none;
`;

const ThemedIconButton = styled(IconButton)`
    background-color: rgb(0, 0, 0, 0);
    color: ${({ theme }) => theme.text};
    &:hover {
        background-color: ${({ theme }) => theme.accent_secondary};
        color: ${({ theme }) => theme.white};
    }
`;

export default class NestedList extends React.Component {

    handleChannelRemove = (e, channel) => {
        e.preventDefault();
        this.removeChannel(channel);
    }

    removeChannel = (channel) => {
        fetch(`/api/admin/channels/remove`, {
            method: 'POST',
            body: JSON.stringify({"text": `${channel}`})
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
        })
        .catch(e => console.log(e));
    }

    muteUser = (username) => {
        fetch(`/api/admin/users/mute`, {
            method: 'POST',
            body: JSON.stringify({"text": `${username}`})
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
        })
        .catch(e => console.log(e));
    }

    deafenUser = (username) => {
        fetch(`/api/admin/users/deafen`, {
            method: 'POST',
            body: JSON.stringify({"text": `${username}`})
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
        })
        .catch(e => console.log(e));
    }

    kickUser = (username) => {
        fetch(`/api/admin/users/kick`, {
            method: 'POST',
            body: JSON.stringify({"text": `${username}`})
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
        })
        .catch(e => console.log(e));
    }

    banUser = (username) => {
        fetch(`/api/admin/users/ban`, {
            method: 'POST',
            body: JSON.stringify({"text": `${username}`})
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            console.log(json_data.status);
        })
        .catch(e => console.log(e));
    }

    render() {
        let collapseIcon = <ExpandMoreRoundedIcon />;
        let expandIcon = <ChevronRightIcon/>;
        if (this.props.users && this.props.items) {
            expandIcon = this.props.users.length > 0 || this.props.items.length > 0 ? <ChevronRightIcon /> : <RemoveRoundedIcon/>;
            collapseIcon = this.props.users.length > 0 || this.props.items.length > 0 ? <ExpandMoreRoundedIcon /> : <RemoveRoundedIcon/>;
        }
        return (
                <ThemedTreeItem key={`${this.props.node_id}-key`} nodeId={this.props.node_id+""} 
                label={this.props.item_label} expandIcon={expandIcon} collapseIcon={collapseIcon}>
                    {
                        this.props.users ?
                        this.props.users.map((user, user_index) => (
                            <ThemedTreeItem 
                            key={`${this.props.node_id+user_index}-key`} 
                            nodeId={this.props.node_id+user_index+""} 
                            label={
                                <Grid container xs={12} key={`${user["name"]}-key`} alignItems="center">
                                    <Grid item sm={8}>
                                        <Typography variant="subtitle2" gutterBottom={true} noWrap>{user["name"]}</Typography>
                                    </Grid>
                                    <Fragment>
                                        {   user["self_mute"] === "True" ?
                                            <Grid item sm={1} alignItems="right">
                                                <MicOffIcon color="secondary" style={{width:"15px", height:"15px"}}/>
                                            </Grid> : ""
                                        }
                                        {   user["server_mute"] === "True" ?
                                            <Grid item sm={1} alignItems="right">
                                                <MicOffIcon color="primary" style={{width:"15px", height:"15px"}}/>
                                            </Grid> : ""
                                        }
                                        {   user["self_deaf"] === "True" ?
                                            <Grid item sm={1} alignItems="right">
                                                <VolumeOffIcon color="secondary" style={{width:"15px", height:"15px"}}/>
                                            </Grid> : ""
                                        }
                                        {   user["server_deaf"] === "True" ?
                                            <Grid item sm={1} alignItems="right">
                                                <VolumeOffIcon color="primary" style={{width:"15px", height:"15px"}}/>
                                            </Grid> : ""
                                        }
                                    </Fragment>
                                </Grid>
                            } 
                            icon={this.props.bot_name === user["name"] ? <DesktopWindowsRoundedIcon/> : <PersonRoundedIcon/>}
                            >
                                {
                                    this.props.bot_name !== user["name"] ?
                                    <Grid container xs={12}>
                                        <Grid item>
                                            <Tooltip title={`Mute ${user["name"]}`} aria-label={`mute-user-${user["name"]}`}>
                                            <ThemedIconButton size="small" onClick={() => this.muteUser(user["name"])}><MicOffIcon style={{width:"15px", height:"15px"}}/></ThemedIconButton>
                                            </Tooltip>
                                        </Grid>
                                        <Grid item>
                                            <Tooltip title={`Deafen ${user["name"]}`} aria-label={`deafen-user-${user["name"]}`}>
                                            <ThemedIconButton size="small" onClick={() => this.deafenUser(user["name"])}><VolumeOffIcon style={{width:"15px", height:"15px"}}/></ThemedIconButton>
                                            </Tooltip>
                                        </Grid>
                                        <Grid item>
                                            <Tooltip title={`Kick ${user["name"]}`} aria-label={`kick-user-${user["name"]}`}>
                                            <ThemedIconButton size="small" onClick={() => this.kickUser(user["name"])}><ExitToAppRoundedIcon style={{width:"15px", height:"15px"}}/></ThemedIconButton>
                                            </Tooltip>
                                        </Grid>
                                        <Grid item>
                                            <Tooltip title={`Ban ${user["name"]}`} aria-label={`ban-user-${user["name"]}`}>
                                            <ThemedIconButton size="small" onClick={() => this.banUser(user["name"])}><BlockRoundedIcon style={{width:"15px", height:"15px"}}/></ThemedIconButton>
                                            </Tooltip>
                                        </Grid>
                                    </Grid> : ""
                                }
                            </ThemedTreeItem>
                        )) : ""
                    }
                    { 
                        this.props.items.map((subitem, subitem_index) => (
                            <NestedList 
                                key={`${(subitem_index+this.props.node_id+1)}-key`}
                                node_id={(subitem_index+this.props.node_id+1) + ""} 
                                items={subitem.subchannels}
                                users={subitem.users}
                                bot_name={this.props.bot_name}
                                item_label={
                                <Grid container xs={12} alignItems="center">
                                    <Grid item sm={11}>
                                        <Typography variant="body2" gutterBottom={true} noWrap>{subitem.channel_name}</Typography>
                                    </Grid>
                                    <Grid item sm={1}>
                                    {
                                        this.props.item_label && !this.props.users ? "" :
                                        <Tooltip title={`Remove Channel: ${subitem.channel_name}`} aria-label={`remove-channel-${subitem.channel_name}`}>
                                            <ThemedIconButton size="small" onClick={(event) => this.handleChannelRemove(event, subitem.channel_name)}><DeleteRoundedIcon style={{width: "20px", height: "20px"}}/></ThemedIconButton>
                                        </Tooltip>
                                    }
                                    </Grid>
                                </Grid>
                                }>
                            </NestedList>
                        ))
                    }
                </ThemedTreeItem>
        );
    }

}